import { motion } from 'framer-motion';
import { useState } from 'react';
import ProjectCard from '@components/ProjectCard';
import ProjectModal from '@components/ProjectModal';
import { projects } from '@utils/mockData';
import { Project } from '@utils/types';

const Projects = () => {
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);

  // If ProjectCard expects category: 'Unity' | 'Unreal Engine', map mockData category accordingly
  const mapCategory = (cat: string): 'Unity' | 'Unreal Engine' => cat === 'Unity' ? 'Unity' : 'Unreal Engine';

  return (
    <div className="min-h-screen p-4">
      <div className="max-w-7xl mx-auto">
        <h1 className="text-4xl md:text-6xl font-bold mb-8 text-center">
          <span className="text-cyber-orange">Projects</span>
        </h1>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {projects.map((project, index) => (
            <motion.div
              key={project.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
            >
              <ProjectCard
                project={{ ...project, category: mapCategory(project.category) }}
                onClick={() => setSelectedProject(project)}
              />
            </motion.div>
          ))}
        </div>
        {selectedProject && (
          <ProjectModal
            project={{ ...selectedProject, category: mapCategory(selectedProject.category) }}
            onClose={() => setSelectedProject(null)}
          />
        )}
      </div>
    </div>
  );
};

export default Projects; 